.page-main {
  background: -webkit-radial-gradient(top left, ellipse, rgb(123, 117, 101) 0%, rgb(51, 86, 106) 40%, rgb(52, 66, 75) 100%);
  background: -o-radial-gradient(top left, ellipse, rgb(123, 117, 101) 0%, rgb(51, 86, 106) 40%, rgb(52, 66, 75) 100%);
  background: radial-gradient(ellipse at top left, rgb(123, 117, 101) 0%, rgb(51, 86, 106) 40%, rgb(52, 66, 75) 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.resizer {
  width: 500px;
  max-width: 100%;
}

/*tabs*/
.page-main .nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.page-main .tabs {
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.page-main .tab-indicator {
  background-color: rgba(255, 255, 255, 0.8) !important;
}

.page-main .tab-menu {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
}

.page-main .tab-menu button {
  margin-right: 8px;
}

.page-main .tab-menu svg {
  font-size: 30px;
}

/*menu*/
#menu * {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 200;
  font-family: 'SF';
}
#menu .container {
  background-color: rgba(51, 83, 101, 0.95);
}
#menu svg {
  margin-right: 12px;
}

/*confirm*/
#dialog > div:first-child {
  background-color: rgba(0, 0, 0, 0.2);
}

#dialog * {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 200;
  font-family: 'SF';
  text-transform: unset;
}

#dialog .paper {
  background-color: rgba(51, 83, 101, 0.95);
  margin: 16px;
}

#dialog .action {
  color: rgb(255, 252, 211);
  font-weight: 600;
}

#dialog button {
  font-size: 16px;
}

/*progress*/
.page-main .fetch-progress {
  width: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.page-main .fetch-progress .container {
  position: absolute;
  width: 100%;
  z-index: 100;
  mix-blend-mode: lighten;
}

.page-main .fetch-progress * {
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  -o-transition-property: transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}

.page-main .fetch-progress .linear {
  -webkit-transform-origin: 50% 0%;
      -ms-transform-origin: 50% 0%;
          transform-origin: 50% 0%;
  background-color: rgb(118, 245, 173);
  overflow-y: visible;
  -webkit-box-shadow: 0 5px 20px rgba(118, 245, 173, 0.7);
          box-shadow: 0 5px 20px rgba(118, 245, 173, 0.7);
}

.page-main .fetch-progress .linear div {
  background-color: rgb(118, 194, 245);
  -webkit-box-shadow: 0 5px 20px rgba(118, 194, 245, 0.7);
          box-shadow: 0 5px 20px rgba(118, 194, 245, 0.7);
}

/*fetch status*/
.page-main .fetch-status {
  margin-top: 30vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 5vh;
  opacity: 0.4;
  font-weight: 200
}

/*Job*/
.job-list .scroll-thumb {
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 4px;
}

.job-list .scroll-view {
  margin-bottom: 50px;
}

.job-list .container {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.job-list .flex {
  padding: 10px;
}

.job-list .footer {
  height: 50px;
}


